import {
  BoxInput,
  Button,
  Form,
  TextInput,
  Title,
  ToolTip,
} from '../styled-components/AuthForm.styled'
import useFetchAndLoad from '../../../hooks/useFetchAndLoad'
import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { useForm } from 'react-hook-form'
import { loadUserToLocalStorageAndCookie } from '../../../utilities/handleStorage.utility'
import { createUser } from '../../../redux/states/user.slice'
import { createUserAdapter } from '../../../adapters/user.adapters'
import { registerUser, uploadImg } from '../../../services/public.services'
import { Avatar, Image, InputImage } from '../../../styled-components/Globals'
import { lazy, useState } from 'react'
const Loading = lazy(() => import('../../../components/Loading'))
import pic_change from '../../../assets/pic_change.png'
import IconSet from './IconSet'
import { Row } from '@/styled-components'

interface Props {
  handleForm: () => void
}

interface Inputs {
  name: string
  email: string
  password: string
  confirm_password: string
}

const Register = ({ handleForm }: Props) => {
  const { loading, callEndpoint } = useFetchAndLoad()
  const [pic, setPic] = useState(pic_change)
  const navigate = useNavigate()
  const dispatch = useDispatch()

  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
  } = useForm<Inputs>()

  const onSubmit = async ({ email, password, name }: Inputs) => {
    const user = await callEndpoint(
      registerUser({ email, password, name, pic })
    )
    loadUserToLocalStorageAndCookie(user)
    dispatch(createUser(createUserAdapter(user)))
    navigate('/')
  }

  const uploadImage = async (files: any) => {
    const response = await callEndpoint(uploadImg(files))
    setPic(response.data.url)
  }

  if (loading) return <Loading></Loading>

  return (
    <Form autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
      <Title color="#278048">Registrarse</Title>
      <IconSet />
      <Avatar htmlFor="input">
        <InputImage
          id="input"
          type="file"
          accept=".jpg,.jpeg,.png"
          onChange={(e: any) => uploadImage(e.target.files[0])}
        ></InputImage>
        <Image src={pic}></Image>
      </Avatar>
      <BoxInput>
        <TextInput
          placeholder="Nombres"
          {...register('name', {
            required: 'Ingrese nombres',
          })}
        ></TextInput>
        {errors.name && <ToolTip text={errors.name.message}></ToolTip>}
      </BoxInput>
      <BoxInput>
        <TextInput
          placeholder="Correo electrnico"
          {...register('email', {
            required: 'Ingrese email',
            pattern: {
              value:
                /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
              message: 'Email invalido',
            },
          })}
        ></TextInput>
        {errors.email && <ToolTip text={errors.email.message}></ToolTip>}
      </BoxInput>
      <BoxInput>
        <TextInput
          type={'password'}
          placeholder="Contrasea"
          {...register('password', {
            required: 'Ingrese contrasea',
          })}
        />
        {errors.password && <ToolTip text={errors.password.message}></ToolTip>}
      </BoxInput>
      <BoxInput>
        <TextInput
          type={'password'}
          placeholder="Confirmar contrasea"
          {...register('confirm_password', {
            required: 'Ingrese contrasea',
            validate: (val: string) => {
              if (watch('password') != val) {
                return 'Las contraseas no coinciden'
              }
            },
          })}
        />
        {errors.confirm_password && (
          <ToolTip text={errors.confirm_password.message}></ToolTip>
        )}
      </BoxInput>
      <Row>
        <Button display="" color="#00CC4B" type="submit">
          Registrarse
        </Button>
        <Button
          display="none"
          onClick={handleForm}
          color="#4a4a4abd"
          type="button"
        >
          Ingresar
        </Button>
      </Row>
    </Form>
  )
}

export default Register
